@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
	@include mkdfRelativeHolderLayout();
	padding: 0;
	box-sizing: border-box;
	
	.mkdf-comment-holder-inner {
		padding: 0 0 20px;
	}
	
	.mkdf-comments-title {
		margin: 0 0 35px 0;
	}
	
	.mkdf-comment {
		
		&.mkdf-pingback-comment {
			
			.mkdf-comment-text {
				padding: 0;
			}
		}
	}
	
	.mkdf-comment-list {
		position: relative;
		padding: 0;
		margin: 0;
		list-style: none;
		z-index: 150;
		
		li {
			@include mkdfRelativeHolderLayout();
			margin: 42px 0 0;
			padding: 1px 0 0;
		}
		
		> li:first-child {
			margin: 0;
			padding: 0;
			border: 0;
		}
		
		.children {
			padding: 0 0 0 120px;
		}
	}
	
	.mkdf-comment-image {
		position: relative;
		top: 0;
		left: 0;
		width: 100px;
		height: 100px;
		display: block;
		float: left;
		
		img {
			display: block;
			border-radius: 0;
		}
	}
	
	.mkdf-comment-text {
		padding: 0 0 0 119px;
		min-height: 82px;
		
		.mkdf-comment-info {
			@include mkdfRelativeHolderLayout();
			margin: 1px 0 0;
		}
		
		.replay,
		.comment-reply-link,
		.comment-edit-link {
			padding-top: 17px;
			padding-right: 10px;
			font-size: 12px;
			color: $default-heading-color;
			font-family: $default-heading-font;
			text-transform: uppercase;
			letter-spacing: 0.2em;
			
			&:hover {
				color: $first-main-color;
			}
		}
		
		.mkdf-comment-name {
			float: left;
			margin: 0;
		}
		
		.mkdf-comment-date {
			float: left;
			margin: 0;
			font-size: 18px;
			font-style: italic;
			
		}
		
		.mkdf-text-holder p {
			margin: 6px 0 15px 0;
		}
		
		.comment-respond {
			margin: 20px 0;
			
			.comment-reply-title {
				margin: 0 0 10px;
				font-size: 14px;
			}
			
			.logged-in-as {
				margin: 0 0 8px;
			}
		}
		
		#cancel-comment-reply-link {
			margin-left: 10px;
			font-size: 12px;
			color: $first-main-color;
			
			&:hover {
				color: $default-heading-color;
			}
		}
		
		ul, ol {
			
			li {
				display: list-item;
			}
		}
		
		li {
			margin: 0 !important;
			padding: 0 !important;
			border: 0 !important;
		}
	}
}

.mkdf-comment-form {
	@include mkdfRelativeHolderLayout();
	margin: 58px 0 40px;
	clear: both;
	
	.comment-reply-title {
		margin: 4px 0 22px;
	}
	
	.logged-in-as {
		margin: 0 0 8px;
	}
}

@include ipad-portrait {
	
	.mkdf-comment-holder {
		
		.mkdf-comment-list {
			
			.children {
				padding: 0 0 0 40px;
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-comment-holder {
		
		.mkdf-comment-list {
			
			.children {
				padding: 0;
			}
		}
		
		.mkdf-comment-text {
			
			.mkdf-comment-date,
			.mkdf-comment-name {
				float: none;
			}
			
			.replay,
			.comment-reply-link,
			.comment-edit-link {
				float: none;
				margin: 0 10px 0 0;
			}
		}
	}
}

@include phone-portrait {
	
	.mkdf-comment-holder {
		
		.mkdf-comment-image {
			float: none;
		}
		
		.mkdf-comment-text {
			padding: 20px 0 0;
			min-height: inherit;
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.mkdf-grid-small-gutter.mkdf-grid-spec {
	margin-left: -8px;
	margin-right: -8px;
	
	.mkdf-grid-col-6 {
		padding-left: 8px;
		padding-right: 8px;
	}
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer &,
		.mkdf-wpcf7-no-margin & {
			margin: 0;
		}
	}
}

.wpcf7-form {
	
	div.wpcf7-validation-errors,
	div.wpcf7-acceptance-missing,
	div.wpcf7-spam-blocked,
	div.wpcf7-aborted,
	div.wpcf7-mail-sent-ok,
	div.wpcf7-mail-sent-ng {
		border: 1px solid $default-heading-color;
	}
	
	div.wpcf7-response-output {
		margin: 1em 0em 1em;
		padding: 0.2em 0.5em;
	}
	
	.mkdf-side-menu &,
	.mkdf-page-footer & {
		
		div.wpcf7-validation-errors,
		div.wpcf7-acceptance-missing,
		div.wpcf7-spam-blocked,
		div.wpcf7-aborted,
		div.wpcf7-mail-sent-ok,
		div.wpcf7-mail-sent-ng {
			border: 1px solid #ccc;
		}
	}
}

.mkdf-custom-contact2-form {

	.mkdf-two-columns-form-without-space {

	    .mkdf-column-right {
	        position: relative;
		    left: -1px;
	    }
	}

	.wpcf7-form-control {
		
		&.wpcf7-text {
			padding: 10px 16px;

			.mkdf-ms-explorer & {
				padding: 14px 16px;
			}
		}
	}

	@include smaller-phone-portrait {

		.mkdf-two-columns-form-with-space,
		.mkdf-two-columns-form-without-space {

			.mkdf-btn {
				width: 100%;
			}
		}
	}
}

.mkdf-cf7-custom-main-home {
	position: absolute;
	bottom: 40px;
	right: 60px;
	max-width: 21%;
	padding: 63px 35px 54px;
	background-color: $default-background-color;
	
	@include laptop-landscape-large {
		max-width: 23%;
	}
	
	@include laptop-landscape-mac {
		bottom: 20px;
		right: 35px;
	}
	
	@include ipad-landscape {
		bottom: 10px;
		right: 26px;
		max-width: 29%;
	}
	
	@include ipad-portrait {
		bottom: 20px;
		right: 37px;
		max-width: 48%;
	}
	
	@include phone-landscape {
		bottom: 10px;
		right: 25px;
		max-width: 77%;
	}
	
	.mkdf-section-title-holder {
		margin: 0 0 24px;
	}
	
	.wpcf7-text {
		margin: 0;
	}
	
	.mkdf-cf7-description {
		@include mkdfHeadingStyle(h5);
		margin: 6px 0 0;
		font-size: 14px;
		text-align: left;
	}
}

.mkdf-cf-custom-newsletter {
	position: relative;
	
	.wpcf7-text {
		padding-right: 54px !important;
		font-size: 18px !important;
		font-weight: 500 !important;
	}
	
	.mkdf-btn-subscribe {
		position: absolute !important;
		top: 0;
		right: 0;
		display: inline-block;
		width: 54px !important;
		height: 56px;
		padding: 0 !important;
		vertical-align: middle;
		font-size: 24px !important;
		line-height: 1 !important;
		letter-spacing: normal !important;
		color: $default-heading-color !important;
		background-color: transparent !important;
		border: none !important;
		
		svg {
			position: relative;
			top: 2px;
			left: -2px;
			fill: $default-heading-color;
			transition: fill .2s ease-out;
		}
		
		&:hover {
			color: $default-text-color !important;
			
			svg {
				fill: $default-text-color;
			}
		}
	}
}

