/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;
	
	.owl-stage-outer {
		z-index: 1;
	}
	
	&.mkdf-slider-is-moving {
		.owl-item {
			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
	
	&.mkdf-slider-hide-pagination .owl-dots {
		display: none;
	}
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			padding: 0;
			margin: 0;
			color: $default-heading-color;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			z-index: 3;
			
			transform: translateY(-50%);
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 46px;
				line-height: 1;
				
				svg g {
					transition: .3s ease-out;
				}
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 30px;

			@include ipad-landscape {
				left: 0;
			}
			
			&:hover {
				span svg g{
					transform: translateX(-4px);
				}
			}
		}
		
		.owl-next {
			right: 30px;

			@include ipad-landscape {
				right: 0;
			}
			
			&:hover {
				span svg g{
					transform: translateX(4px);
				}
			}
		}
	}
	
	.mkdf-nav-light-skin & {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				color: $header-light-color;
				
				&:hover {
					color: $header-light-color;
				}
			}
		}
	}
	
	.mkdf-nav-outside-slider & {
		
		.owl-nav {
			
			@include phone-portrait {
				display: none;
			}
			
			.owl-prev {
				left: -47px;
			}
			
			.owl-next {
				right: -47px;
			}
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 23px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			padding: 0;
			margin: 0 6px;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 9px;
				height: 9px;
				border-radius: 50%;
				background-color: transparent;
				border: 1px solid $default-heading-color;
				box-sizing: border-box;
				
				transition: background-color .2s ease-out, border-color .2s ease-out;
			}
			
			&:hover,
			&.active {
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}
	
	.mkdf-pag-light-skin & {
		
		.owl-dots {
			
			.owl-dot {
				
				span {
					border: 1px solid $header-light-color;
				}
				
				&:hover,
				&.active {
					
					span {
						background-color: $header-light-color;
						border-color: $header-light-color;
					}
				}
			}
		}
	}
	
	.mkdf-pag-inside-slider & {
		
		.owl-dots {
			position: absolute;
			left: 50%;
			bottom: 10px;
			margin: 0;
			z-index: 1;
			transform: translateX(-50%);
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}
	
	+ .mkdf-slider-thumbnail {
		display: inline-block;
		vertical-align: middle;
		list-style: none;
		margin: 20px -10px 0;
		
		> .mkdf-slider-thumbnail-item {
			float: left;
			padding: 0 10px;
			margin-bottom: 20px;
			box-sizing: border-box;
			line-height: 0;
			cursor: pointer;
			
			img {
				border: 2px solid transparent;
				
				transition: border-color 0.2s ease-out;
			}
			
			&.active {
				img {
					border: 2px solid $first-main-color;
				}
			}
		}
		
		$thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($thumbnail_columns_number) {
			&.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {
				
				.mkdf-slider-thumbnail-item {
					width: 100% / ($i+2);
					
					@include ipad-landscape {
						width: 33.3333%;
					}
				}
			}
		}
	}
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
	.mkdf-wrapper {
		padding: 0 !important;
	}
	
	.mkdf-content {
		height: 100vh;
	}
	
	&.admin-bar {
		.mkdf-content {
			height: calc(100vh - 32px);
		}
	}
	
	.mkdf-content-inner {
		@include mkdfTableLayout();
	}
	
	.mkdf-page-not-found {
		@include mkdfTableCellLayout();
		background-color: #feeae7;
		padding: 7% 20% 0;
		text-align: center;
		
		.mkdf-404-title {
			margin: 0;
			font-family: $custom-font;
			font-size: 100px;
			line-height: 63px;
			color: $default-background-color;
		}
		
		.mkdf-404-subtitle {
			margin: 0;
			font-size: 30px;
			letter-spacing: 0.05em;
			color: $default-heading-color;
		}
		
		.mkdf-404-text {
			margin: 18px 0 0;
			font-size: 22px;
			font-style: italic;
			color: $default-heading-color;
		}
		
		.mkdf-btn {
			margin: 44px 0 0;
			
			&.mkdf-btn-light-style {
				color: #585858;
				background-color: #fff;
				border-color: #fff;
				
				&:hover {
					color: #fff !important;
					background-color: transparent !important;
					border-color: #fff !important;
				}
			}
		}
	}

	@include laptop-landscape-medium {
		.mkdf-page-not-found {
			padding: 10% 20% 0;
		}
	}
	
	@include ipad-landscape {
		.mkdf-content {
			height: 100%;
			min-height: 100vh;
		}
		
		&.admin-bar {
			.mkdf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
		
		.mkdf-page-not-found {
			padding: 23.1% 10%;
		}
	}
	
	@include ipad-portrait {
		.mkdf-page-not-found {
			padding: 46.5% 10%;
		}
	}
	
	@include phone-landscape {
		.mkdf-page-not-found {
			padding: 50% 10%;
		}
	}
	
	@include phone-portrait {
		.mkdf-page-not-found {
			padding: 42.6% 10%;
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
	position: fixed;
	width: 50px;
	height: 50px;
	right: 60px;
	bottom: 53px;
	margin: 0;
	z-index: 10000;
	opacity: 1;
	visibility: hidden;
	
	&.mkdf-back-to-top-footer {
		position: absolute;
		bottom: 54px;
	}
	
	&.mkdf-back-to-top-light {
		&:hover{
			>span {
				color: $default-text-color;
				border-color: $default-text-color;
			}
		}
	}
	
	&.off {
		opacity: 0;
		visibility: hidden;
		
		transition: opacity 0.15s ease, visibility .0s .15s;
	}
	
	&.on {
		opacity: 1;
		visibility: visible;
		
		transition: opacity 0.3s ease;
	}
	
	> span {
		position: relative;
		display: block;
		width: 100%;
		height: 100%;
		padding-left: 2px;
		@include mkdfHeadingStyle(h6);
		line-height: 48px;
		text-align: center;
		color: #5f5f5f;
		background-color: transparent;
		border: 1px solid #5f5f5f;
		overflow: hidden;
		box-sizing: border-box;
		transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
		
		i {
			transition: .15s ease .05s;
		}
		
	}
	
	span span {
		display: block;
		line-height: inherit;
	}
	
	&:hover {
		> span {
			color: $default-heading-color;
			border-color: $default-heading-color;
		}
	}
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

