/* ==========================================================================
   #Background SVG styles - begin
   ========================================================================== */
.mkdf-row-bg-svg-holder {
	width: 100%;
	position: relative;
	//visibility: hidden;
	
	.touchevents & {
		visibility: visible;
		
		.mkdf-row-bg-svg {
			opacity: 1;
			
			svg {
				transform: rotate(0) scale(1);
			}
		}
	}
	
	.mkdf-row-bg-svg {
		position: absolute;
		//opacity: 0;
		overflow: hidden;
		transition: 1.5s cubic-bezier(0, 0, .11, 1.1);
		
		svg {
			transform-origin: 50% 50%;
			transform: rotate(180deg) scale(.5);
			transition: 1.5s cubic-bezier(0, 0, .11, 1.1);
		}
		
		@include ipad-landscape {
			display: none;
		}
	}
	
	.no-touchevents &.mkdf-appeared {
		visibility: visible;
		
		.mkdf-row-bg-svg {
			opacity: 1;
			
			svg {
				transform: rotate(0) scale(1);
			}
		}
	}
}

/* ==========================================================================
   #Background SVG styles - end
   ========================================================================== */

/* ==========================================================================
   #Background Text styles - begin
   ========================================================================== */
.mkdf-row-bg-text-holder {
	position: absolute;
	bottom: 0;
	right: 0;
	//width: 100%;
	//visibility: hidden;
	
	.touchevents & {
		visibility: visible;
	}
	
	.mkdf-row-bg-text-inner {
		box-sizing: border-box;
		
		.no-touchevents & {
			overflow: hidden;
		}
	}
	
	.mkdf-char-mask {
		display: inline-block;
		vertical-align: middle;
		overflow: hidden;
	}
	
	.mkdf-char {
		display: inline-block;
	}
	
	&.mkdf-from-left {
		
		.no-touchevents & .mkdf-char {
			opacity: 0;
			transform: translate3d(-100%, 0, 0);
		}
	}
	
	&.mkdf-from-right {
		
		.no-touchevents & .mkdf-char {
			opacity: 0;
			transform: translate3d(100%, 0, 0);
		}
	}
	
	.mkdf-row-bg-text {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		padding: 0 60px;
		font-family: $custom-font;
		font-size: 54px;
		font-weight: 400;
		line-height: 2.2;
		color: $default-heading-color;
		opacity: 1;
		overflow: hidden;
		
		@include ipad-landscape {
			display: none;
		}
	}
	
	.no-touchevents &.mkdf-appeared {
		visibility: visible;
		
		.mkdf-row-bg-text .mkdf-char {
			opacity: 1;
			transform: translate3d(0, 0, 0);
			transition: transform .6s cubic-bezier(.05, .43, .04, .87), opacity .4s cubic-bezier(0.19, 0.5, 0.46, 0.88);
		}
	}
}

/* ==========================================================================
   #Background Text styles - end
   ========================================================================== */
/* ==========================================================================
   #Custom styles - begin
   ========================================================================== */
.mkdf-custom-wishlist-header {
	
	a {
		position: relative;
		display: inline-block;
		vertical-align: middle;
	}
	
	span {
		font-size: 18px;
		font-style: italic;
		padding-right: 7px;
		color: $default-heading-color;
		position: relative;
		display: inline-block;
		vertical-align: middle;
	}
	
	svg path {
		fill: transparent;
		transition: .2s ease-out;
	}
	
	&:hover {
		svg path {
			fill: currentColor;
		}
	}
}

