/* ==========================================================================
   #Animation styles - start
   ========================================================================== */
@include keyframes(mkdf-animate-burger-line) {
	0% {
		transform: scaleX(1);
		transform-origin: 100% 0;
	}
	
	33% {
		transform: scaleX(0);
		transform-origin: 100% 0;
	}
	
	40% {
		transform: scaleX(0);
		transform-origin: 0 0;
	}
	
	100% {
		transform: scaleX(1);
		transform-origin: 0 0;
	}
}

@include keyframes(mkdf-fade-in) {
	0% {
		opacity: 0;
		
	}
	100% {
		opacity: 1;
	}
}

@include keyframes(element-from-left) {
	0% {
		opacity: 0;
		transform: translate(-20%, 0);
	}
	100% {
		opacity: 1;
		transform: translate(0, 0);
	}
}


@include keyframes(element-from-right) {
	0% {
		opacity: 0;
		transform: translate(20%, 0);

	}
	100% {
		opacity: 1;
		transform: translate(0, 0);
	}
}

@include keyframes(element-from-top) {
	0% {
		opacity: 0;
		transform: translate(0, -20px);

	}
	100% {
		opacity: 1;
		transform: translate(0, 0);
	}
}

@include keyframes(element-from-bottom) {
	0% {
		opacity: 0;
		transform: translate(0, 20px);

	}
	100% {
		opacity: 1;
		transform: translate(0, 0);
	}
}


.mkdf-element-from-left,
.mkdf-element-from-right,
.mkdf-element-from-top,
.mkdf-element-from-bottom,
.mkdf-element-from-fade {
	@include mkdfRelativeHolderLayout();
	
	> div {
		@include mkdfRelativeHolderLayout();
		opacity: 0;
	}
}

.mkdf-element-from-fade {

	&.mkdf-element-from-fade-on {

		> div {
			opacity: 1;
			transition: opacity .8s ease 0s;
		}
	}
}

.mkdf-element-from-left {

	&.mkdf-element-from-left-on {

		> div {
			opacity: 1;
			animation: element-from-left 0.55s 1 ease;
		}
	}
}

.mkdf-element-from-right {

	&.mkdf-element-from-right-on {

		> div {
			opacity: 1;
			animation: element-from-right 0.55s 1 ease;
		}
	}
}

.mkdf-element-from-top {

	&.mkdf-element-from-top-on {

		> div {
			opacity: 1;
			animation: element-from-top 0.55s 1 ease;
		}
	}
}

.mkdf-element-from-bottom {

	&.mkdf-element-from-bottom-on {

		> div {
			opacity: 1;
			animation: element-from-bottom 0.55s 1 ease;
		}
	}
}

.mkdf-flip-in {
	opacity: 0;
	transform: perspective(1000px) rotateY(25deg);
	transition: all 1.1s ease-in-out;

	&.mkdf-flip-in-on {
		opacity: 1;
		transform: rotateY(0deg);
	}
}

.mkdf-grow-in {
	opacity: 0;
	transform: scale(0.8);
	transition: all 1s cubic-bezier(0,1,.5,1);

	&.mkdf-grow-in-on {
		opacity: 1;
		transform: scale(1);
	}
}

.mkdf-z-rotate {
	opacity: 0;
	transform: rotateZ(7deg);
	transform-origin: top right;
	transition: all 300ms cubic-bezier(0.165, 0.84, 0.44, 1);

	&.mkdf-z-rotate-on {
		opacity: 1;
		transform: rotateZ(0deg);
	}
}

.mkdf-x-rotate {
	opacity: 0;
	transform: perspective(900px) rotateX(-45deg);
	transition: all .6s cubic-bezier(.785,.135,.15,.86);

	&.mkdf-x-rotate-on {
		opacity: 1;
		transform: rotateX(0deg);
	}
}

.mkdf-y-translate {
	opacity: 0;
	transform: translate(0,30px);
	transition: all 700ms cubic-bezier(0.680, -0.650, 0.265, 1.650);

	&.mkdf-y-translate-on {
		opacity: 1;
		transform: translate(0,0);
	}
}

.mkdf-fade-in-down {
	opacity: 0;
	transform: translate(0,-25px);
	transition: all .9s cubic-bezier(0,1,.5,1);

	&.mkdf-fade-in-down-on {
		opacity: 1;
		transform: translate(0,0);
	}
}

.mkdf-fade-in-left-x-rotate {
	opacity: 0;
	transform: rotateZ(-5deg) translate(-30px,0);
	transition: all .4s ease;

	&.mkdf-fade-in-left-x-rotate-on {
		opacity: 1;
		transform: rotateZ(0deg) translate(0,0);
	}
}

.mkdf-fade-in {
	opacity: 0;
	transition: all 1s ease;

	&.mkdf-fade-in-on {
		opacity: 1;
	}
}
/* ==========================================================================
   #Animation styles - end
   ========================================================================== */